.form-container{
    height: 100%;
    padding: 2rem;
    overflow: auto;
    align-items: baseline;
    flex-wrap: wrap;
    justify-content: center;

    h1{
        color: darkred;
        justify-content: center;
        width: 100%;
    }

    .form{
        overflow: auto;
        flex-wrap: wrap;
        height: 80%;
        width: 80%;
        justify-content: center;
        align-items: center;
        border: .2rem var(--brownColor) solid;
        background: var(--whiteColor);
        border-radius: .8rem;
        -webkit-border-radius: .8rem;
        -moz-border-radius: .8rem;
        -ms-border-radius: .8rem;
        -o-border-radius: .8rem;

        form{
            width: 86%;
            height: 95%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            padding: 1rem;
            border: .1rem var(--brownColor) solid;
            border-radius: .8rem;
            -webkit-border-radius: .8rem;
            -moz-border-radius: .8rem;
            -ms-border-radius: .8rem;
            -o-border-radius: .8rem;

            .boxes-container{
                height: 70%;
                justify-content: space-between;
                flex-wrap: wrap;
                align-items: flex-start;

                .box{
                    width: 40%;
                    align-self: baseline;
                    text-align: start;
                    flex-wrap: wrap;
                    align-items: self-start;
                    justify-content: center;
                    color: black;

                    label{
                        font-size: var(--h2FontSize);
                        color: var(--blackColor);
                        font-weight: 500;
                        width: 100%;
                        padding: 1rem;
                    }

                    input{
                        padding: 1rem;
                        border-radius: .5rem;
                    }

                    .article{
                        display: flex;
                        align-items: baseline;
                        height: 13rem;
                    }

                    .error{
                        color: red;
                        font-size: 10px;
                        margin-top: 0;
                        font-weight: lighter;
                        text-transform: lowercase;
                    }
                }
            }
            .btn-container{
                display: flex;
                justify-content: center;
                width: 100%;

                button{
                    right: 33%;
                    bottom: 2rem;
                    width: 35%;
                    align-self: end
                }
            } 
        }
    }
}


@media screen and (max-width:450px) {
    form{
        overflow: auto;
    }
    
    .box{
        width: 95%!important;
        /* height:0; */
    }

    .btn-container{
        height: 80%!important;
    }
    
}